.post{
    max-width: 645px;
    background-color: #ffffff;
    border: 1px solid lightgray;
    /*margin: 0 auto;*/
    margin-bottom: 45px;
    padding-left: 0!important;
    margin-left: 0!important;
}

.post__image{
    object-fit: contain;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    /*height: 400px;*/
    width: 100%;

}
.post__text{
    font-weight: normal;
    padding: 20px;
}
.post__header{
    display: flex;
    align-items: center;
    padding: 20px;
}
.post__avatar{
    margin-right: 10px;
}
.post__commentBox{
    display: flex!important;
    padding-top: 10px;
    margin: 10px;
    justify-content: space-between;
    border-top: 1px solid lightgray;
}
.like{
    display: flex;
    justify-content: space-between;
    padding: 10px;
}
i{
    font-size: 25px;
}
.first i {
    margin-left: 15px;
}
.first i:first-child{
    margin-left: 0!important;
}
.first i:nth-child(2){
   color: red;

    margin-left: 0!important;
}
.post__comments{
    padding: 20px;
}
.post__input{
    width: 100%;
    padding: 10px;
    border: none;
}

.post__button{
   color: blueviolet;
    font-weight: 700;
    border: none;
    background-color: #ffffff;
}