*{
    margin: 0;
}
.app{
    background-color: #fafafa;
    /*height: 100vh;*/
}
.app__header{
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #ffffff;
    padding: 20px;
    border-bottom: 1px solid lightgray;
    object-fit: contain;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.icons i {
    margin-left: 15px;
    cursor: pointer;
}
.message{
    position: relative;
}
.avatars{
    width: 60%;
    background-color: #ffffff;
    padding: 20px;
    margin-top: 20px;
}
.avatars .MuiAvatar-root{
    width: 70px;
    height: 70px;
    border: 4px solid #e95653;
}
.number{
    position: absolute;
    right: -10px;
    top: -5px;
    width: 20px;
    height: 20px;
    color: #ffffff;
    background-color: red;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
    font-size: 12px;
}
.app__signup{
    display: flex;
    flex-direction: column;
}
.app__headerImage{
    object-fit: contain;
}
.app__posts{
    padding: 20px;
    display: flex;
}
.headerThree{
    text-align: center;
    padding: 20px;
    font-size: 30px;
    color: red;
    font-weight: 900;
}

@media (max-width: 768px) {
    .text-center{
        display: none;
    }
    .icons i{
        font-size: 20px;
        margin-left: 10px;
    }
    .app__posts{
        padding: 0;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .avatars{
        width: 100% !important;
    }
}